import React, { Fragment } from "react";
import { gql, useQuery } from "@apollo/client";
import { useParams, Link } from "react-router-dom";
import classNames from "classnames";

const LAUNCH_QUERY = gql`
  query LaunchQuery($flight_number: Int!) {
    launch(flight_number: $flight_number) {
      flight_number
      mission_name
      launch_year
      launch_success
      launch_date_local
      rocket {
        rocket_id
        rocket_name
        rocket_type
      }
    }
  }
`;

const Launch = (props) => {
  let { flight_number } = useParams();
  flight_number = parseInt(flight_number);
  let launchData;
  const { loading, error, data } = useQuery(LAUNCH_QUERY, {
    variables: { flight_number },
  });

  if (loading) {
    launchData = (
      <div className="spinner-grow" role="status">
        <span className="visually-hidden">Loading...</span>
      </div>
    );
  }
  if (error) {
    launchData = (
      <p className="lead">
        There is an error in your syntax. Please check the log
      </p>
    );
    console.log(error);
  }
  if (data) {
    const {
      mission_name,
      flight_number,
      launch_year,
      launch_success,
      rocket: { rocket_id, rocket_name, rocket_type },
    } = data.launch;
    launchData = (
      <div>
        <h1 className="display-4 my-3">
          Mission: <span>{mission_name}</span>
        </h1>
        <h4 className="mb-3">Launch Details</h4>
        <ul className="list-group">
          <li className="list-group-item mb-2">
            Flight Number: {flight_number}
          </li>
          <li className="list-group-item mb-2">Launch Year: {launch_year}</li>
          <li className="list-group-item mb-2">
            Launch Successful:{" "}
            <span
              className={classNames({
                "text-success": launch_success,
                "text-danger": !launch_success,
              })}
            >
              {launch_success ? "Yes" : "No"}
            </span>
          </li>
        </ul>
        <h4 className="my-3">Rocket Details</h4>
        <ul className="list-group">
          <li className="list-group-item mb-1">RocketID: {rocket_id}</li>
          <li className="list-group-item mb-1">Rocket Name: {rocket_name}</li>
          <li className="list-group-item mb-1">Rocket Type: {rocket_type}</li>
        </ul>
        <hr />
        <Link to="/" className="btn btn-primary">
          Back to HomePage
        </Link>
      </div>
    );
  }
  return (
    <Fragment>
      <h1>Launch</h1>
      {launchData}
    </Fragment>
  );
};

export default Launch;
